.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.visual {
  background: url(./img/visual.jpg) no-repeat top / cover;
  text-align: left;
  color: #fff;
}

.products img {
  width: 100%;
}
.products h4 {
  margin-top: 20px;
  line-height: 1;
}
.products p {
  margin-bottom: 20px;
  font-size: 12px;
  line-height: 1;
}
.products .price {
  margin-bottom: 50px;
  font-size: 16px;
  font-weight: 700;
}
